.main-footer
{
  background-color: @red-color;
  color: @scin-color;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}
.footer__container
{
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  margin: 10px 15px;
}
.footer-social
{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 100px;
  margin: 10px auto;
  list-style: none;
  padding: 0;
}
.author
{
  margin: 10px auto;
}
.author__name
{
  text-decoration: none;
  color: @scin-color;
  font-family: Roboto-Mono, sans-serif;
  font-size: 11px;
  & span:hover
  {
    color: @main-font-color;
    border-bottom: 1px dashed @main-font-color;
  }
}

.social__git
{
  background-image: url("../../img/socgit.png");
  &:hover
  {
    background-color: @main-font-color;
    border-radius: 15px;
  }
  &:active
  {
    opacity: 0.7;
  }
}
.social__42
{
  background: url("../../img/soc42.png");
  &:hover
  {
    background-color: @main-font-color;
  }
  &:active
  {
    opacity: 0.7;
  }
}
.social__insta
{
  background: url("../../img/socinst.png");
  &:hover
  {
    background-color: @main-font-color;
    border-radius: 10px;
  }
  &:active
  {
    opacity: 0.7;
  }
}
.footer-social__link
{
  width: 20px;
  height: 20px;
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: contain;
  display: block;
  margin: 0 5px;
}
@media (min-width: @tablet-width)
{
  .author__name
  {
    font-size: 15px;
  }
  .footer-social__link
  {
    width: 30px;
    height: 30px;
  }

}

