.sign-in-form-container
{
  margin: 15px auto;
  padding: 15px 15px;
  display: flex;
  flex-direction: column;
  border: 2px solid @main-font-color;
  border-radius: 5px;
  max-width: 300px;
  &:hover, &:active
  {
    box-shadow: -2px 3px 15px 2px rgba(240,70,84,0.67);
  }
}
.sign-in-form
{
  margin: 0;
  padding: 0;
}
.sign-in-form__header
{
  padding: 0;
  text-align: center;
  margin: 0 0 10px 0;
}
.sign-in-form__label
{
  font-family: Roboto-Thin, sans-serif;
  display: block;
  font-size: 11px;
  font-style: italic;
  margin-bottom: 2px;
}
.sign-in-form__email, .sign-in-form__pass
{
  box-sizing: border-box;
  margin-bottom: 5px;
  border:none;
  box-shadow: 0 0 1px @main-font-color inset;
  background-color: bisque;
  border-radius: 5px;
  width: 100%;
  max-width: 300px;
  outline: none;
  padding: 5px;
  color: @main-font-color;
  font-size: 13px;
  &:hover
  {
    box-shadow: 0 0 2px @main-font-color inset;
  }
  &:active, &:focus
  {
    box-shadow: 0 0 4px @main-font-color inset;
  }
}
.sign-in-form__email
{
  background-image: url("../../img/icon-backpack.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 99% 0;
  &:hover, &:active
  {
    background-blend-mode: hard-light;
  }
}
.sign-in-form__pass
{
  background-image: url("../../img/icon-art.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 99% 1px;
  &:hover, &:active
  {
    background-blend-mode: hard-light;
  }
}

.sign-in-form__help-container
{
  margin-bottom: 10px;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
.sign-in-form__forgot-reg
{
  color: @main-font-color;
  font-family: Roboto-Thin, sans-serif;
  display: block;
  font-size: 10px;
  font-style: italic;
  margin-bottom: 2px;
  padding-right: 5px;
  &:hover
  {
    font-size: 11px;
    text-decoration: none;
    font-style: normal;
  }
  &:active
  {
    opacity: 0.7;
  }
}
.sign-in-form__submit
{
  background-color: @red-color;
  padding: 6px 23px;
  border-radius: 14px;
  display: block;
  margin: 0 auto;
  color: @scin-color;
  border: none;
  outline: none;
  &:hover
  {
    background-color: @main-font-color;
    opacity: 0.7;
    box-shadow: -2px 3px 15px 2px rgba(240,70,84,0.67);
  }
  &:active
  {
    opacity: 1;
  }
}
@media (min-width: @tablet-width)
{
  .sign-in-form-container
  {
    margin-top: 50px;
    max-width: 400px;
    margin-bottom: 20px;
    min-height: 310px;
  }
  .sign-in-form__header
  {
    font-size: 25px;
    margin-bottom: 20px;
  }
  .sign-in-form__label
  {
    font-size: 15px;
    margin-bottom: 5px;
  }
  .sign-in-form__email, .sign-in-form__pass
  {
    max-width: 100%;
    padding: 10px;
    font-size: 19px;
    background-size: 7%;
    background-position: 99% 60%;
  }
  .sign-in-form__forgot-reg
  {
    font-size: 15px;
    margin-bottom: 15px;
    &:hover
    {
      font-size: 18px;
      margin-bottom: 0;
    }
  }
  .sign-in-form__submit
  {
    font-size: 20px;
    padding: 10px 35px;
    border-radius: 23px;
    margin-bottom: 5px;
    margin-top: 15px;
  }
}
