.navigation__container
{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin:0;
  padding: 0;
}
.navigation__logo-inner
{
  margin: 0;
  padding: 0;
}
.user-navigation__menu, .main-navigation__menu
{
  margin:0;
  padding: 0;
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;

}

.user-navigation__menu li, .main-navigation__menu li
{
  list-style: none;
  text-decoration: none;
  padding: 0 5px;

}
.user-navigation__link a , .main-navigation__link a
{
  font-size: 10px;
  text-decoration: none;
  text-transform: uppercase;
  color: @scin-color;
  &:hover, &:focus
  {
    color: @links-hover;
    border-bottom: 1px dashed @links-hover;
  }
  &:active
  {
    color: @main-font-color;
  }
}

@media (min-width: @tablet-width)
{
  .navigation__container
  {
    justify-content: space-between;
    padding-top: 10px;
  }
  .user-navigation__link a , .main-navigation__link a
  {
    padding: 15px;
    font-size: 15px;
  }
  .user-navigation__link a
  {
    padding-left: 25px;
    &:after
    {
      position: absolute;
      content:"";
      top: 2px;
      left: 5px;
      width: 20px;
      height: 20px;
      background: url("../../img/login.png") no-repeat 0 0;
      background-size: contain;
      display: block;
      cursor: pointer;
      &:hover, &:active
      {
        background: url("../../img/login_hover.png");
        background-position: 0 0;
        background-repeat: no-repeat;
        background-size: contain;
        display: block;
        border:1px solid black;
      }
    }
  }
  .user-navigation__link
  {
    position: relative;
  }
}
